.search-bar-cmp {
  form {
    width: 300px;
    height: 40px;
    position: relative;
    margin: auto;
    margin-top: 10px;
    // Moblie
    // margin-bottom: 25px;
    // margin-left: 25px;

    @include for-wide-layout {
      form {
      }
    }

    input {
      width: 100%;
      height: 100%;
      padding: 0 50px 0 20px;
      border-radius: 25px;
      border: none;
      outline: none;
      box-sizing: border-box;
      font-size: 16px;
      background-color: rgba(255, 255, 255, 0.1);
    }

    a.search-button {
      width: 50px;
      height: 40px;
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      overflow: hidden;
      border-radius: 25px;
      background-color: #fff;
      text-decoration: none;
      background-color: rgba(255, 255, 255, 0.1);

      .icon {
        width: 14px;
        height: 14px;
        position: absolute;
        top: 11px;
        left: 15px;
        background-color: #eff4f6;
        border-radius: 8px;
        border: 2px solid #607d8b;

        &:after {
          width: 12px;
          height: 2px;
          display: block;

          content: '';
          position: absolute;
          bottom: -5px;
          right: -8px;
          background-color: #607d8b;
          transform: rotate(45deg);
          transition: all 0.6s ease;
        }
      }
    }
  }
}
